html {
  font-family: "Roboto Condensed", Arial, Verdana, Geneva, Tahoma, sans-serif;
  color: #000;
  min-height: 100%;
  min-width: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: auto;
  box-sizing: border-box;
  background-color: #fff;
}

*:focus {
  outline: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

*::selection,
*::-moz-selection {
  background: #9effff;
  color: #000;
}

body {
  width: 100%;
  min-height: 100%;
  background: #fff;
  text-align: center;
  overflow-x: hidden;
  font-size: 16px;
}
